import React from 'react';

class Header extends React.Component  {
  state = {
    words: [
      ["Привет!", "это сайт", "RUDEBOX!"],
      ["Мы стараемся", "помочь", "создать сайт"],
      ["Я надеюсь", "мои труды вам помогут"],
      ["Также", "спасибо", "кто нажал"],
      ["на рекламу", "это нам", "очень помогает"],
      ["Всем", "хорошего", "дня, друзья!"],
      ["а также", "отличного", "настроения"]
    ],
    currentIndex: 0,
    style: 't3xt-in',
  }

  componentDidMount() {
    setInterval(this.changeCurrentIndex, 5000)
  }

  changeCurrentIndex = () => {
    const {words, currentIndex} = this.state;
    if(currentIndex === words.length - 1) {
      this.setState({currentIndex: 0});
    }else{
      this.setState((prev) => ({currentIndex: prev.currentIndex +=1}));
    }
  }


 
render() {
const {words,currentIndex} =this.state;
  return (
    <header className="cd-header" data-type="slider-item" >
       <h1>UX works for</h1> 
  <div className="dropping-texts">
  <div>Developers</div>
  <div>Designers</div>
  <div>Coders</div>
  <div>EVERYONE!</div>
  </div>
  <h2 className="sprite"></h2>
    </header> 
  );
 }
}

export default Header;